
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
}
@media print
{
.no-print, .no-print *
    {
        display: none !important;
}
}
@media screen
{
.no-screen, .no-screen *
    {
        display: none !important;
}
}



